.form-container {
  display: flex;
  flex-direction: column;
}

.form-item {
  opacity: 0;
  max-height: 0;
  -webkit-transition: opacity 0.3s linear, max-height 0.3s linear;
  transition: opacity 0.3s linear, max-height 0.3s linear;
}

.form-item.active {
  opacity: 1;
  max-height: 200%;
  -webkit-transition: opacity 0.3s linear, max-height 0.3s linear;
  transition: opacity 0.3s linear, max-height 0.3s linear;
}
